import './TaskRow.css';
import Delete from '../Components/Images/Delete.png';
import Done from '../Components/Images/Done.png';
import { connect } from 'react-redux';

const TaskRow = (props)=> {
    return (
       <div className="task">
            <h4 className = "taskName"> {props.name} </h4>
            <div className = "buttonsImg">
                <img src={Delete} alt={"Delete"} className="deleteBtn" onClick={() => props.deleteTask(props.id)}/>
                {
                    props.status === 'pending' ? <img src={Done} alt={"Done"} className="doneBtn"
                    onClick={ () => props.markDoneTask(props.task)}/>  : null
                }
            </div>
        </div>
    );
}

const mapDispatchToProps = dispatch => {
    return {
      deleteTask: (id) => dispatch({ type: 'DELETE_TASK', payload: id }),
      markDoneTask: (doneTask) => dispatch({ type: 'DONE_TASK', payload: doneTask })
    }
  }
  
export default connect(null, mapDispatchToProps)(TaskRow);