import React from 'react';
import Logo from '../Components/Images/Logo.png';
import { useState } from 'react'
import { connect } from 'react-redux';
import Button from 'react-bootstrap/Button';
import './TaskForm.css';

const TaskForm = (props) => {
    const [taskNameInput, setNewTaskNameInput] = useState('');
    const [taskAlertMsg, setTaskNewAlertMsg] = useState('');
    const [displayErrorMsg, setDisplayErrorMsg] = useState(false);
    const [displayInput, setDisplayInput] = useState(false);
    
    //DISPLAY INPUT TASK
    const displayInputHandler = () => {
        setTaskNewAlertMsg('');
        setDisplayInput(true);
        setDisplayErrorMsg(false)
    }

    // FOR RESET BUTTON
    const resetInputForm = () => {
        setNewTaskNameInput('')
        setDisplayInput(false);
        setDisplayErrorMsg(false);
    }

    //FOR INPUT TASK / BINDING
    const taskNameInputChangeHandler = (e) => {
        setNewTaskNameInput(e.target.value)
        setDisplayErrorMsg(false);
    }

    //FOR ADD TASK BUTTON
    const addTaskBtnClickHandler = () => {
        
            //CHECK IF EMPTY
        if (taskNameInput.trim() === '') {
            setDisplayErrorMsg(true);
            setTaskNewAlertMsg("This must be filled out")

            //CHECK IF DUPLICATE DATA
        } else if (props.tasks.some(task => taskNameInput.toLowerCase().trim() === task.name.toLowerCase())) {
            setNewTaskNameInput('');
            setDisplayErrorMsg(true);
            setTaskNewAlertMsg('Task already exists')

            //CHECK IF OKAY DATA
        } else {
            setNewTaskNameInput('');
            setDisplayInput(false);
            setDisplayErrorMsg(false);
            props.addNewTask(taskNameInput)
        }
    }

    return (
        <div className="formWrapper">
            <div className="form">
                <div className="header">
                    <img src={Logo} alt={"Logo"} className="logo"/>
                    <h2 className="formTitle">{props.title}</h2>
                </div>
                    {
                        displayInput === true ?
                            <div>
                            <input type="text" onChange={taskNameInputChangeHandler}
                                value={taskNameInput}
                                placeholder="Add task"/>
                            </div>
                            : null
                    }
                { // FOR ALERT ERROR MESSAGE
                    displayErrorMsg === true?
                        <p className="error">{taskAlertMsg}</p> : null
                }
                { // FOR SHOW/HIDE OF INPUT TASK
                    displayInput !== true ?
                        <Button variant="primary" className="displayInputBtn"
                            onClick={displayInputHandler}>{props.use}
                        </Button> :
                        <div>
                            <Button variant="outline-primary" className="addTaskBtn"
                                onClick={addTaskBtnClickHandler}> Add Task
                            </Button>
                            <Button variant="outline-primary" className="addTaskBtn"
                                onClick={resetInputForm}> Reset
                            </Button>
                        </div> 
                }
            </div>
        </div>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        addNewTask: (taskNameInput) => dispatch({ type: 'ADD_NEW_TASK', payload: taskNameInput}),
    }
}
export default connect(null, mapDispatchToProps)(TaskForm);