import { v4 as uuidv4 } from 'uuid';

const initialState = {
    tasks: [
        { id: uuidv4(), name: 'eat', status: 'pending' },
        { id: uuidv4(), name: 'code', status: 'pending' },
        { id: uuidv4(), name: 'sleep', status: 'done' }
    ],
};

const TasksReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'ADD_NEW_TASK':
            let newTasks = { id: uuidv4(), name: action.payload, status: 'pending' } 
            return {
                ...state,
                tasks: [...state.tasks, newTasks],
            }
        case 'DELETE_TASK':
            let tasksList;
            let popUpMessage = window.confirm("Are you sure to delete this task?");
            if (popUpMessage === true) {
                tasksList = state.tasks.filter((task) => task.id !== action.payload);
            } else {
                return state;
            }
                return {
                    ...state,
                    tasks: tasksList
                }
        case 'DONE_TASK':
            let doneTask = action.payload;
            let doneFilter = state.tasks.filter((task) => task.id !== doneTask.id)
            let addDone = { id: doneTask.id, name: doneTask.name, status: 'done' }
            let doneFilterTask = [...doneFilter, addDone]
            return {
                ...state,
                tasks: doneFilterTask,
            }
        default:
            return state;
    }
}
export default TasksReducer;