import './TaskTable.css';
import TaskRow from './TaskRow';
import Card from 'react-bootstrap/Card';

const TaskTable = ({ tasks, title, deleteTask, markDoneTask, mainTasks }) => {
  return (
    <Card className="mainCard text-center">
      <Card.Header className="cardHeader"> <b>{title} ({tasks.length})</b></Card.Header>
          <Card.Body className="cardBody">
            { 
              tasks.map( task => {
                return <TaskRow
                  task={task}
                  name={task.name}
                  key={task.name}
                  id={task.id}
                  status={task.status}
                  mainTasks={mainTasks}
                  deleteTask={deleteTask}
                  markDoneTask={markDoneTask}
                />
              })
            }
        </Card.Body>
    </Card>
    );
}
export default TaskTable;