import React from 'react';
import TaskTable from './Components/TaskTable';
import TaskForm from './Components/TaskForm';
import Header from './Components/Header';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import { connect } from 'react-redux';
import './App.css';

const App = (props) => {

  let pendingTasks = props.tasks.filter(task => task.status === 'pending');
  let done = props.tasks.filter(task => task.status === 'done');

return (
  <div className="App">
    <Container fluid className="p-0">
      <Header/>
      <Col className="m-3 m-md-auto col-md-9 col-lg-7 col-xl-6">
        <TaskForm tasks={props.tasks} title="Add tasks" use="+" />
      </Col>
      <Col className="m-3 col-sm-12 col-md-8 m-md-auto col-lg-6 col-xl-4">
        {pendingTasks.length !== 0 ?
          <TaskTable
            allTasks = {props.tasks}
            tasks = {pendingTasks}
            title = "Pending Tasks" />
          : <Card className="emptyDisplay text-center"> <b>NO PENDING TASKS</b> </Card>}
        {done.length !== 0 ?
          <TaskTable
            tasks={done}
            title="Done Tasks" />
          : <Card className="emptyDisplay text-center"> <b>NO DONE TASKS</b> </Card>}
      </Col>
    </Container>
  </div>
  );
}

const mapStateToProps = state => {
  return {
    tasks: state.tasks
  }
}
export default connect(mapStateToProps)(App);